body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color:  #333745;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

:root {
  --color-main: #333745;
  --color-scnd: #e63462;
}

#editor {
  white-space: pre-line;
  resize: none;
  box-sizing: border-box;
  display: -webkit-flex;
  display: flex;
  width: 100%;
  height: 45em;
  background-color: #333745;
  background-color: var(--color-main);
  color: #79ccc5;
  border: 1em solid #e63462;
  border: 1em solid var(--color-scnd);
  border-radius: 1em;
}

:root {
  --color-main: #333745;
}

#preview {
  white-space: pre-line;
  overflow: scroll;
  height: 35em;
  width: 100%;
  text-align: justify;
  word-break: break-all;
  padding: 0.5em;
  box-sizing: border-box;
}

#preview > * {
  margin: 0;
}

table,
td,
th {
  border: solid 2px #333745;
  border: solid 2px var(--color-main);
  border-collapse: collapse;
  text-align: center;
}

pre {
  background-color: #333745;
  background-color: var(--color-main);
  color: #c7efcf;
  padding: 1em;
}

a {
  color: #e63462;
}
p {
  margin: 0;
}
blockquote {
  color: #333745;
  color: var(--color-main);
  padding-left: 1em;
  border-left: solid 0.2em #e63462;
  margin: 1em !important;
}

ul,
ol,
li {
  line-height: 0.8em;
}

:root {
  --color-bg: #eef5db;
  --color-border: #e63462;
  --color-main: #333745;
  --color-gh: #fe5f55;
  --border-size: 0.8em;
}

.parent {
  text-align: center;
  background-color: #333745;
  background-color: var(--color-main);
  color: #333745;
  color: var(--color-main);
}

.container {
  display: -webkit-inline-flex;
  display: inline-flex;
  -webkit-flex-flow: row wrap;
          flex-flow: row wrap;
  -webkit-justify-content: center;
          justify-content: center;
  width: 100%;
}

.editor-item {
  width: 30em;
  margin: 0.5em;
  -webkit-flex: 1 1;
          flex: 1 1;
}

.preview-item {
  border: 0.8em solid #e63462;
  border: var(--border-size) solid var(--color-border);
  border-radius: 1em;
  margin: 0.5em;
  background-color: #eef5db;
  background-color: var(--color-bg);
  color: #333745;
  color: var(--color-main);
  -webkit-flex: 2 1;
          flex: 2 1;
}

.gh-item,
h4 {
  color: #eef5db;
  color: var(--color-bg);
  margin: 0;
}

.gh-item a {
  margin: 1em;
  text-decoration: none;
  background-color: #fe5f55;
  background-color: var(--color-gh);
  border-radius: 1em;
  color: #333745;
  color: var(--color-main);
  padding: 0.5em;
  font-weight: bold;
}

.gh-item i {
  padding: 0.3em;
  background-color: #333745;
  background-color: var(--color-main);
  color: #fe5f55;
  color: var(--color-gh);
  border-radius: 100%;
  font-weight: normal !important;
}

@media (max-width: 768px) {
  /* For mobile phones: */
  .container {
    width: auto;
  }
}

